.cost__title {
  font-size: 25px;
  text-align: center;
}

.form__input__item {
  display: flex;
  flex-direction: column;
  margin: 10px 0;
}

.form__input__item label {
  margin-bottom: 10px;
  font-size: 18px;
}

.form__input__item input {
  margin-bottom: 10px;
  height: 30px;
  border-radius: 8px;
  border: none;
  padding: 10px;
  font-size: 18px;
}

.form__btn {
  margin: 0 auto;
  display: block;
  padding: 15px 20px;
  border-radius: 8px;
  border: none;
  background-color: white;
  margin-bottom: 20px;
  font-size: 18px;
}